      var state = getSearchState(cm)
      var query = parseQuery(rawQuery, !!ignoreCase, !!smartCase)
      if (!query) {
        return
      }
      highlightSearchMatches(cm, query)
      if (regexEqual(query, state.getQuery())) {
        return query
      }
      state.setQuery(query)
      return query
